import { useState } from "react";
function Main(props) {
  const [mode, setMode] = useState(false);
  const handleMode = (flag) => {
    // flag = true
    if (flag) {
      setMode(true);
    } else {
      setMode(false);
    }
  };
  const go = [
    require("../images/shortcut1.png"),
    require("../images/shortcut2.png"),
    require("../images/shortcut3.png"),
    require("../images/shortcut4.png"),
    require("../images/shortcut5.png"),
  ];
  const bbs = [
    {
      title: "Lorem ipsum dolor sit amet, consectetur adipiscing",
      regdate: " 2024.03.01",
      link: "/notice1",
    },
    {
      title: " consectetur adipiscLorem ipsum dolor sit amet,ing",
      regdate: " 2024.03.01",
      link: "/notice2",
    },
    {
      title: " consectetur a amet,ing",
      regdate: " 2024.03.01",
      link: "/notice3",
    },
    {
      title: " consectetur a amet,ing onsectetur adipiscLorem ipsum dolo",
      regdate: " 2024.03.01",
      link: "/notice4",
    },
  ];

  const gallery = [
    require("../images/gallery1.jpg"),
    require("../images/gallery2.jpg"),
    require("../images/gallery3.jpg"),
  ];
  return (
    <main>
      <article>
        <ul>
          {go.map((src, idx) => (
            <li key={idx}>
              <a href="#">
                <img src={src} alt="" />
              </a>
            </li>
          ))}
        </ul>
      </article>
      <article>
        <h2>Lorem ipsum, dolor sit amet consectetur adipisicing elit. Odio.</h2>
      </article>
      <article>
        <div className="tab">
          <h3 onClick={() => handleMode(false)}></h3>
          <h3 onClick={() => handleMode(true)}></h3>
        </div>
        <div className="notice" style={{ display: mode ? "none" : "block" }}>
          <ul>
            {bbs.map((item, idx) => {
              if (idx === 0) {
                return (
                  <li key={idx}>
                    <p>
                      <a
                        href={item.link}
                        onClick={(e) => {
                          e.preventDefault();
                          props.setIsPopup(true);
                        }}
                      >
                        {item.title}
                      </a>
                    </p>
                    <p>{item.regdate}</p>
                  </li>
                );
              } else {
                return (
                  <li key={idx}>
                    <p>
                      <a href={item.link}>{item.title}</a>
                    </p>
                    <p>{item.regdate}</p>
                  </li>
                );
              }
            })}
          </ul>
        </div>
        <div className="gallery" style={{ display: mode ? "block" : "none" }}>
          <ul>
            {gallery.map((src, index) => (
              <li key={index}>
                <a href="#">
                  <img src={src} alt="" />
                </a>
              </li>
            ))}
          </ul>
        </div>
      </article>
    </main>
  );
}

export default Main;
