function Header() {
  const logo = require("../images/logo.png");
  const menu = [
    {
      mainMenu: "",
      link: "/home",
      subMenu: [
        {
          title: "",
          link: "/home2",
        },
        {
          title: "CEO",
          link: "/ceo",
        },
        {
          title: "",
          link: "/history",
        },
        {
          title: "",
          link: "/oragan",
        },
      ],
    },
    {
      mainMenu: "",
      link: "/company2",
      subMenu: [
        {
          title: "",
          link: "/menu2-1",
        },
        {
          title: "",
          link: "/menu2-2",
        },
        {
          title: "",
          link: "/menu2-3",
        },
        {
          title: "",
          link: "/menu2-4",
        },
      ],
    },
    {
      mainMenu: "",
      link: "/notice_list",
      subMenu: [
        {
          title: "",
          link: "/menu3-1",
        },
        {
          title: "",
          link: "/menu3-2",
        },
        {
          title: "",
          link: "/menu3-3",
        },
        {
          title: "/",
          link: "/menu3-4",
        },
      ],
    },
    {
      mainMenu: "",
      link: "/pr",
      subMenu: [
        {
          title: "",
          link: "/menu4-1",
        },
        {
          title: "",
          link: "/menu4-2",
        },
        {
          title: "CI/BI",
          link: "/menu4-3",
        },
      ],
    },
  ];
  return (
    <header>
      <h2>
        <a href="#">
          <img src={logo} alt="" />
        </a>
      </h2>
      <ul>
        {menu.map((item, idx) => (
          <li key={idx}>
            <a href={item.link}>{item.mainMenu}</a>
            <div>
              {item.subMenu.map((subItem, subidx) => (
                <a href={subItem.link} key={subidx}>
                  {subItem.title}
                </a>
              ))}
            </div>
          </li>
        ))}
      </ul>
    </header>
  );
}

export default Header;
